import React, { FC, ReactElement, useState } from 'react';

import TextInput from '../../components/Inputs/TextInput';
import InputSelect from '../../components/Inputs/InputSelect';
import Heading from '../../components/Heading/Heading';

import { Qualifications } from '../../types/TransferTypes';

type SuspendedTeacherListProps = {
  InternalRequested: [];
  qualification: Qualifications[];
};

const SuspendedTeacherList: FC<SuspendedTeacherListProps> = ({
  InternalRequested,
  qualification,
}): ReactElement => {
  const [search, SetSearch] = useState<string>('');
  const [selected_level, setSelected_level] = useState<string>('');

  return (
    <>
      <div className='mt-3'>
        <div className='border border-t mt-2 mb-1 flex items-center px-3 rounded'>
          <div className='flex-1 pr-2'>
            <TextInput
              error=''
              label='Search'
              name=''
              onChange={(e) => SetSearch(e.target.value)}
              placeholder='Search by district name'
              value={search}
              size='sm'
            />
          </div>
          <div className=''>
            <InputSelect
              data={[
                { title: 'All qualifications', value: '' },
                ...qualification.map((el) => ({
                  title: el.qualification_name,
                  value: el.qualification_id,
                })),
              ]}
              error=''
              name='select_level'
              onChange={(e) => setSelected_level(e.target.value)}
              label='Qualification'
              placeholder='Select'
              value={selected_level}
              size='sm'
            />
          </div>
        </div>
        <table className='w-full text-left'>
          <thead>
            <tr className='text-center'>
              <th>#</th>
              <th>First Name</th>
              <th>Last Name</th>
              <th>Qualification</th>
              <th>Position</th>
              <th>Current School</th>
              <th>Requested Date</th>
              <th>Status</th>
              <th className='w-80'>Reason</th>
            </tr>
          </thead>
          <tbody>
            {InternalRequested.length <= 0 ? (
              <tr>
                <td
                  colSpan={6}
                  className='p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center'
                >
                  <Heading size='md'>No results found</Heading>
                </td>
              </tr>
            ) : (
              <></>
            )}
          </tbody>
        </table>
      </div>
    </>
  );
};

export default SuspendedTeacherList;
