import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, Basic } from "../../actions";
import { StoreState } from "../../reducers";
import axios from "axios";

import Container from "../../components/ContainerComponents/Container";

import { setAxiosToken } from "../../utils/AxiosToken";
import { Qualifications } from "../../types/TransferTypes";
import TerminatedList from "./TerminatedList";
import SuspendedTeacherList from "./SuspendedTeacherList";
import { APP_URL } from "../../config/app.config";

type SuspendedTeacherListProps = {
  auth: Auth;
  basic: Basic;
};

type SuspendedTeacherListState = {
  loading: boolean;
  openTab: number;
  qualification: Qualifications[];
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
};

class _SuspendedTeacherListClass extends Component<
  SuspendedTeacherListProps,
  SuspendedTeacherListState
> {
  constructor(props: SuspendedTeacherListProps) {
    super(props);
    this.state = {
      openTab: 1,
      loading: false,
      qualification: [],
      error: {
        target: null,
        msg: "",
      },
    };
  }

  // get qualification Handler
  getQualification() {
    setAxiosToken();
    axios
      .get(`${APP_URL}/qualifications`)
      .then((response) => response.data)
      .then((data: Qualifications[]) => {
        this.setState({
          qualification: data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  componentDidMount = () => {
    this.getQualification();
  };

  render() {
    return (
      <>
        <Container
          title={`Suspended / Terminate (${this.props.basic.academic_calender?.academic_year_name})`}
          subTitle=""
        >
          {/* Tab */}
          <div className="flex flex-wrap">
            <div className="w-full">
              <ul
                className="flex mb-0 list-none flex-wrap pt-3 pb-4 flex-row"
                role="tablist"
              >
                <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                  <a
                    className={
                      "text-xs font-bold uppercase px-5 py-3 rounded block leading-normal " +
                      (this.state.openTab === 1
                        ? "text-white bg-accent-800"
                        : "text-slate-600 bg-gray-400")
                    }
                    onClick={(e) => {
                      e.preventDefault();
                      this.setState({
                        openTab: 1,
                      });
                    }}
                    data-toggle="tab"
                    href="#link1"
                    role="tablist"
                  >
                    List of Suspended Teachers
                  </a>
                </li>
                <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                  <a
                    className={
                      "text-xs font-bold uppercase px-5 py-3 rounded block leading-normal " +
                      (this.state.openTab === 2
                        ? "text-white bg-accent-800"
                        : "text-slate-600 bg-gray-400")
                    }
                    onClick={(e) => {
                      e.preventDefault();
                      this.setState({
                        openTab: 2,
                      });
                    }}
                    data-toggle="tab"
                    href="#link2"
                    role="tablist"
                  >
                    List Of Terminated Teachers
                  </a>
                </li>
              </ul>
              {/* Body */}
              <div className="relative flex flex-col min-w-0 break-word w-full">
                <div className="px-4 py-5 flex-auto">
                  <div className="tab-content tab-space">
                    {this.state.openTab === 1 && (
                      <div id="link1">
                        <SuspendedTeacherList
                          InternalRequested={[]}
                          qualification={this.state.qualification}
                        />
                      </div>
                    )}
                    {this.state.openTab === 2 && (
                      <div id="link2">
                        <TerminatedList
                          InternalRequested={[]}
                          qualification={this.state.qualification}
                        />
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Container>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const SuspendedTeacherListClass = connect(
  mapStateToProps,
  {}
)(_SuspendedTeacherListClass);
